import { fundRecordList } from '@/api/payApi.js'
export default {
	data() {
		return {
			is50: true,
			pageIndex:1,
			records:[]
		};
	},
	components: {},
	onLoad() {
		
	},
	onShow() {
		//  获取账单列表
		this.getFundRecordList()
	},
	methods:{
		//  获取账单列表
		getFundRecordList(){
			let data = {
				pageIndex: this.pageIndex
			}
			fundRecordList(data).then(res=>{
				if(res.code == '1000') {
					this.records = res.data
				}
			})
		},
		// 去订单详情
		goDetail(){
			uni.navigateTo({
				url:'/pages/middle/release/account/record/detail'
			})
		}
	}
}
